import React from "react";
import img9 from "../Static/lock-7-xxl.png";
import img1 from "../Static/—Pngtree—the light bulb is full_5225125.png";
import img2 from "../Static/make a stor.png";
import img3 from "../Static/—Pngtree—blue purple book education information_5374873.png";
import img8 from "../Static/2650152.jpg";
import img4 from "../Static/60285-business-icons-idea-computer-brainstorming-social.png";
import img5 from "../Static/different-squares.png";

import imgswif from "../Static/icons/icons8-react-200.png";
import imggit from "../Static/icons/icons8-html-5-144.png";
import imgapp from "../Static/icons/icons8-css3-144.png";
import imggcordata from "../Static/icons/icons8-firebase-144.png";

export class MakeAStory extends React.Component {

    componentDidMount() {

    }

    render() {
        return (
            <div className="MakeStory">
                <div className="StoryHeader">
                    <div>
                <h2>Improve your English Skills with</h2>
                <h1>MAKE A STORY</h1>
                    </div>
                <img src={img3} className="red"  alt="logo" />
                    </div>
                <img src={img2} className="StoryBigIMG"  alt="logo" />

                <div className="StoryRules">
         <h1>HOW TO PLAY</h1>
                   <div>
                    <h3> 1 Generate game code</h3>
                    <h3>2 Get two random Words</h3>
                   </div>
                    <div>

                    <h3>3 Write a sentence which
                        has to continue the story</h3>
                    <h3>4 Sent game kod
                        to your friend</h3>
                    </div>
                    <h3>5 Wait for they to continue!</h3>
                </div>


                <div className="StoryLevels">
                        <h1>LEVELS</h1>

                    <div>

                    <h3>Beginner</h3>
                    <h3>Intermediate</h3>
                    <h3>Advanced</h3>
                    </div>
                </div>

                <div className="StoryItems">

                    <div>
                        <img src={img4} className="App-logo"  alt="logo" />
                        <h1>LEARN
                            <br/>
                            <span>new words</span>
                        </h1>
                    </div>

                    <div>
                        <img src={img1} className="App-logo"  alt="logo" />
                        <h1>PRACTICE
                            <br/>
                            <span>creative thinking</span></h1>
                    </div>

                    <div>
                        <img src={img8} className="App-logo"  alt="logo" />
                            <br/>
                            <br/>
                        <h1>HAVE FUN
                            <br/>
                            <span>with your friends</span> </h1>
                    </div>

                </div>

                <div className="MakeStoryIcons">
                    <div>
                        <img src={imgswif} className="App-logo"  alt="logo" />
                        <img src={imggcordata} className="App-logo"  alt="logo" />
                        <img src={imggit} className="App-logo"  alt="logo" />
                        <img src={imgapp} className="App-logo"  alt="logo" />

                    </div>
                </div>


            </div>
        );
    }


}

